/* 全局样式 */
.App {
  text-align: center;
}

/* 基础部分样式 */
.section {
  margin-bottom: 4rem;
  position: relative;
}

/* 英雄区域样式 */
.hero-section {
  background: linear-gradient(135deg, #00c2ff 0%, #3a72d9 100%);
  color: white;
  padding: 3rem 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 194, 255, 0.3);
}

/* 卡片网格布局 */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

/* 现代化元素 */
.gradient-text {
  background: linear-gradient(90deg, #00c2ff, #ff0099);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
  font-weight: bold;
}

.glass-effect {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(0, 194, 255, 0.2);
  border-radius: 16px;
}

.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 194, 255, 0.3);
}

/* 动画效果 */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes neonPulse {
  0% { box-shadow: 0 0 10px rgba(0, 194, 255, 0.5), 0 0 20px rgba(0, 194, 255, 0.3); }
  50% { box-shadow: 0 0 15px rgba(0, 194, 255, 0.7), 0 0 30px rgba(0, 194, 255, 0.5); }
  100% { box-shadow: 0 0 10px rgba(0, 194, 255, 0.5), 0 0 20px rgba(0, 194, 255, 0.3); }
}

@keyframes glowingBorder {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.animate-in {
  animation: fadeIn 0.8s ease forwards;
}

.neon-element {
  animation: neonPulse 2s infinite;
  border: 1px solid rgba(0, 194, 255, 0.3);
}

/* AI风格装饰元素 */
.ai-decoration {
  position: absolute;
  z-index: 0;
  filter: blur(40px);
  opacity: 0.2;
}

.ai-decoration-1 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, #00c2ff 0%, #ff0099 100%);
  top: -150px;
  right: -100px;
  border-radius: 40% 60% 60% 40% / 60% 30% 70% 40%;
  animation: morphBlob 12s ease-in-out infinite alternate;
}

.ai-decoration-2 {
  width: 200px;
  height: 200px;
  background: linear-gradient(135deg, #ff0099 0%, #00c2ff 100%);
  bottom: -100px;
  left: -50px;
  border-radius: 60% 40% 30% 70% / 40% 50% 50% 60%;
  animation: morphBlob 15s ease-in-out infinite alternate;
}

.tech-circuit {
  position: absolute;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 194, 255, 0.5), transparent);
  z-index: 0;
}

.tech-circuit-1 {
  width: 150px;
  top: 30%;
  left: 0;
}

.tech-circuit-2 {
  width: 300px;
  top: 60%;
  right: 0;
}

.tech-dot {
  position: absolute;
  width: 6px;
  height: 6px;
  background-color: rgba(0, 194, 255, 0.8);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 194, 255, 0.8);
}

@keyframes morphBlob {
  0% {
    border-radius: 40% 60% 60% 40% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 70% 70% 30% / 40% 50% 50% 60%;
  }
  100% {
    border-radius: 60% 40% 40% 60% / 50% 60% 40% 50%;
  }
}

/* 响应式调整 */
@media (max-width: 768px) {
  .hero-section {
    padding: 2rem 0;
  }
  
  .ai-decoration-1, .ai-decoration-2 {
    width: 150px;
    height: 150px;
  }
}

/* 现代化滚动条 */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #eef5ff;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #00c2ff;
  border-radius: 4px;
  opacity: 0.7;
}

::-webkit-scrollbar-thumb:hover {
  background: #52e8ff;
}

/* 科技感数据网格背景 */
.cyber-grid {
  position: relative;
}

.cyber-grid::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    linear-gradient(rgba(0, 194, 255, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 194, 255, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  z-index: 0;
  pointer-events: none;
}
